import React, { Component } from "react";
import { ViewPager, Frame, Track, View, } from 'react-view-pager'
import 'bootstrap/dist/css/bootstrap.min.css';
import Paper from '@material-ui/core/Paper';
import Tabs from '@material-ui/core/Tabs';
import Tab from '@material-ui/core/Tab';
import RecentPosts from "./RecentPosts";


export default class HomeScreen extends Component {
    constructor(props) {
        super(props)
    
        this.state = {
             value:[]

        }
    }
    
  render() {
    const firstPAge = () => {
        this.track.scrollTo(0)

      };
      const secondPage = ()=>{
          this.track.scrollTo(1);
          
      }
      const thirdPage=()=>{
        this.track.scrollTo(2)
      }

  const handleChange = (event, newValue) => {
    this.setState({value:newValue});
    console.log(newValue)
    console.log('test')
  };

  
  
    return ( 
      <div className="container" style={{alignItems:"center", alignContent:"center",justifyContent:"center",}}>
        <ViewPager initialPage={0}>
        <div
              style={{
                display: "grid",
                color: "white",
                height: "10vh",
                backgroundColor: "rgb(57, 55, 119)"
              }}
              className="homeHeader">
              <h3 style={{ margin: "auto" }}>HOME SCREEN</h3>
            </div>
        <Paper  square>
              <Tabs
              style={{width:"100%", margin:"auto"}}
                value={this.state.value}
                indicatorColor="primary"
                textColor="primary"
                onChange={handleChange}
                // aria-label="disabled tabs example"
                
              >
                <Tab label="RECENT" onClick={firstPAge}/>
                <div className="container" style={{width:"100%"}}></div>
                <Tab label="LOCATION" onClick={secondPage}/>
                <div className="container" style={{width:"100%"}}></div>
                <Tab label="MINE" onClick={thirdPage}/>
              </Tabs>
            </Paper>
          <Frame>
            <Track
              ref={c => (this.track = c)}
              viewsToShow={1}
              // infinite
              className="track"
              
            >
              <View className="view" key="1">
                <RecentPosts/>
              </View>
              <View className="view" key="2">
                <RecentPosts/>
              </View>
              <View className="view" key="3">
                <RecentPosts/>
              </View>
            </Track>
          </Frame>
          
        </ViewPager>
      </div>
    );
  }
}
